
.App-header {
  background-color: #282c34;
  color: white;
  z-index: 1;
  position: relative;
}

.App-header h1{
  padding: .5em;
  margin-top: 0;
}

main {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

@font-face {
  font-family: cuteFont;
  src: url('./img/Daydreamer.ttf');
}

h2 {
  font-family: cuteFont;
  margin-top: 15%;
  font-size: 7em;
  color: white;
}

.App-background {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
